}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  background-color:  #ddeefc;
}

.button-standard {
  padding: 0.5rem 1rem;
  background-color: bisque;
  text-decoration: none;
  font-size: 2rem;
  border-radius: 200px;
  color: rgb(10, 3, 102);
  color: var(--color-primary);
}
