.footer{display:grid;grid-template-columns:1fr 5rem;grid-template-rows:repeat(2, -webkit-max-content);grid-template-rows:repeat(2, max-content);background-color:var(--color-primary);color:white}.footer--column{max-width:25rem;min-width:15rem;width:15%}.footer--columns-container{grid-column:1/2;grid-row:1/2;display:flex;flex-wrap:wrap;justify-content:flex-end}.footer--header{font-size:2.5rem;margin:1rem 0}.footer--item{list-style:none}.footer--link,.footer--link:link,.footer--link:visited{color:white;text-decoration:none;font-size:1.5rem}.footer--contact{font-size:2.5rem;grid-row:2/3;grid-column:1/2}.footer--social{font-size:2rem;grid-row:1/3;grid-column:2/3}.footer--sociallink{display:block}

.feedback
{
    max-width:700px;
}
.modal-title
{
    font-size: 25px;
    display:block;
    width: 29%;
    margin:auto;
}
.close{
    margin:0px !important;
    padding-left: 0px !important;
}

. {
    box-sizing: border-box;
}
#container {
    display:flex;
    align-items: stretch;
    /* background-color: gainsboro; */
    width: 100%;
    min-height: 600px;
    font-size: 20px;
    font-family: sans-serif;
    flex-wrap: wrap;
}
#profile{
    flex-grow: 1;
    background-color: #f5f5f5d3;
    flex-direction: column;
    padding-top: 40px;
}
#profilephoto{
    display: block;
    margin: auto;
    padding: 10px;
    width: 250px;
    height: 250px;
}

#profile-description{
    max-width: 200px;
    margin-top: 20px;
    margin: auto;
    width: inherit;
    word-wrap: break-word;
    padding: 20px;
    text-align: center;
}
#main{
    flex-grow:10;
}

#course-details{
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;    
    min-height: 580px;
}
#course-desc{
    background-color: wheat;
    flex-grow:2;
    position: relative;
}
#course-video{
    flex-grow:1.5;
}
#about{
    margin-left: 50px;
    padding-top: 40px;
}

body {
    background-color: #eee;
  }
  .reviews {
    width: 600px;
    height: 150px;
    overflow: hidden;
    background-color: rgba(255, 255, 255, 0.74);
    border-radius: 5px;
    margin: 50px auto;
    position: relative;
    box-shadow: 0 0 10px -2px #ddd;
  }
  .panel {
    width: 100%;
    max-height: 100%;
    position: absolute;
  }
  .PanelAct {
    left: 0;
  }
  .panel img {
    width: 100px;
    height: 100px;
    text-align: center;
    float: left;
    margin: 25px;
  }
  /*== Removal Start
  png images background
  ==*/
  .panel:nth-of-type(1) img {
    background: #12aedb;
  }
  .panel:nth-of-type(2) img {
    background: #db4646;
  }
  .panel:nth-of-type(3) img {
    background: #96c465;
  }
  /*== Removal End==*/
  
  .panel p {
    float: left;
    width: 420px;
    clear: right;
    text-align: justify;
    margin: 25px 0 10px 0;
    font-size: 14px;
    color: #aaa;
    line-height: 1.2em;
  }
  .panel h7 {
    float: left;
    width: 200px;
    font-size: 14px;
    color: #999;
    font-weight: bold;
    font-style: italic;
  }
  .control {
    position: absolute;
    bottom: 20px;
    right: 30px;
  }
  .control span {
    background-color: rgba(0, 0, 0, 0.1);
    padding: 5px;
    display: inline-block;
    vertical-align: top;
    margin-left: 5px;
    border-radius: 50%;
    cursor: pointer;
    transition: 0.3s ease-in;
  }
  .ContActive {
    background-color: #bbb !important ;
  }
  
 * {
    box-sizing: border-box;
  }

  input,
  button {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
    border-style: none;
  }

  html {
    font-size: 16px;
    font-family: Roboto, Open Sans, Segoe UI, sans-serif;
    font-weight: 500;
    font-style: normal;
    text-rendering: optimizeLegibility;
    height: 100%;
  }
 
  body {
    height: 100%;
    margin: 0;
  }
 
